import React, { Component } from 'react';
import '../css/Login.css';
import { connect } from "react-redux";
import {Redirect } from "react-router-dom";
import {authLogin} from '../store/actions/auth';



class Login extends Component {
   state = {
      username: "",
      password: ""
    };
  
    handleChange = e => {
      this.setState({ [e.target.name]: e.target.value });
    };
  
    handleSubmit = e => {
      e.preventDefault();
      const { username, password } = this.state;
      this.props.login(username, password);
    };
  

  render() {
   const {token} = this.props;
   const { username, password } = this.state;
   if (token) {
     return <Redirect to="/" />;
   }
    return (
      <div className="row">
      <div className="col-md-6 offset-md-3">
      <form onSubmit={e => this.handleSubmit(e, this.state)}>
      <center><h2><strong>Login</strong></h2></center>
                           <div className="form-group">
                              <label>Email address</label>
                              <input type="text" name="username" value={username} onChange={this.handleChange} className="form-control" placeholder="Enter Username"></input>
                           </div>
                           <div className="form-group">
                              <label>Password</label>
                              <input type="password" name="password" value={password} onChange={this.handleChange} className="form-control" placeholder="Enter Password"></input>
                           </div>
                           <div className="form-group">
                              <p className="text-center">By signing up you accept our <a href="/">Terms Of Use</a></p>
                           </div>
                           <div className="col-md-12 text-center ">
                              <button type="submit" className=" btn btn-block mybtn btn-primary tx-tfm">Login</button>
                           </div>
                           <div className="col-md-12 ">
                              <div className="login-or">
                                 <hr className="hr-or"></hr>
                                 <span className="span-or">or</span>
                              </div>
                           </div>
                           <div className="col-md-12 mb-3">
                              <p className="text-center">
                                 <a href="/" className="google btn mybtn"><i className="fa fa-google-plus">
                                 </i> Signup using Google
                                 </a>
                              </p>
                           </div>
                           <div className="form-group">
                              <p className="text-center">Don't have account? <a href="/signup" id="signup">Sign up here</a></p>
                           </div>
                        </form>
      </div>
      </div>
                   
    )
  }
}

const mapStateToProps = state => {
   return {
     token: state.auth.token
   };
 };
 
 const mapDispatchToProps = dispatch => {
   return {
     login: (username, password) => dispatch(authLogin(username, password))
   };
 };
 
 export default connect(mapStateToProps,mapDispatchToProps)(Login);