import React from 'react';
import '../css/Signup.css';
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { authSignup } from "../store/actions/auth";
 
  
class Signup extends React.Component {
  state = {
    username: "",
    password: ""
  };

  handleSubmit = e => {
    e.preventDefault();
    const { username,password } = this.state;
    this.props.signup(username, password);
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  
  render() {
    const { username, password } = this.state;
    const { token } = this.props;
    if (token) {
      return <Redirect to="/" />;
    }
      return (
        <div className="row">
        <div className="col-md-6 offset-md-3">
        <form onSubmit={e => this.handleSubmit(e, this.state)}>
        <center><h2><strong>Signup</strong></h2></center>
        <div className="form-group">
            <label>Email address</label>
            <input type="text" name="username" value={username} onChange={this.handleChange} className="form-control" placeholder="Enter Username"></input>
        </div>
        <div className="form-group">
            <label>Password</label>
            <input type="password" name="password" value={password} onChange={this.handleChange} className="form-control" placeholder="Enter Password"></input>
        </div>
        <div className="col-md-12 text-center mb-3">
            <button type="submit" className=" btn btn-block mybtn btn-primary tx-tfm">Get Started For Free</button>
        </div>
        <div className="col-md-12 ">
            <div className="form-group">
              <p className="text-center"><a href="/login" id="signin">Already have an account?</a></p>
            </div>
        </div>
      </form>
            
        </div>
      </div>
      
      );
    }
  }
const mapStateToProps = state => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    token: state.auth.token
  };
};

const mapDispatchToProps = dispatch => {
  return {
    signup: (username, password) =>
      dispatch(authSignup(username, password))
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(Signup);