import React, { Component } from 'react'
import axios from 'axios';
import '../css/Detailview.css';
import contact from '../images/contact.png';
import {productDetailURL} from '../constants';

class Detailview extends Component {

  state = {
    data: []
  };

  componentDidMount() {
    this.handleFetchItem();
  }

  handleFetchItem = () => {
    const {
      match: { params }
    } = this.props;
    axios
      .get(productDetailURL(params.productID))
      .then(res => {
        this.setState({ data: res.data });
      })
      
      
  };
    render() {
        const {data} = this.state;
        const item = data;
        return (
        <div className="container">
          <div className="row" style={{marginTop:"20px", marginBottom:"20px"}}>
            <div className="col-9">
            <div className="media">
         <img src={item.imageurl1} style={{borderRadius:"5px"}} className="align-self-start mr-3" alt="..."></img>
        <div className="media-body">
        <h2 className="mt-0"><b>{item.title}</b></h2>
        <h3><b>Price:  $ {item.price} </b></h3>
        <p style={{whiteSpace:"pre-line"}}>{item.description}</p>
        <button onClick={() => this.props.history.push(`cart/`)}type="button" className="btn btn-primary btn-lg btn-block">Add to Cart</button>
        </div>
        </div>
            </div>
            <div className="col-3">
            <div className="card">
              <img src={contact} alt="..."  style={{width: '200px',height: '200px'}}></img>
              <div className="card-body">
                <h5 className="card-title">24/7 Available</h5>
                <p className="card-text"></p>
              </div>
              <ul className="list-group list-group-flush">
                <li className="list-group-item">Cras justo odio</li>
                <li className="list-group-item">Dapibus ac facilisis in</li>
                <li className="list-group-item">Vestibulum at eros</li>
              </ul>
              <div className="card-body">
              <button type="button" className="btn btn-primary btn-sm btn-block">Contact us</button>
              </div>
            </div>
            </div>
          </div>
  

          <div className="card text-center">
  <div className="card-header">
    <ul className="nav nav-tabs card-header-tabs">
      <li className="nav-item">
        <a className="nav-link" href="/">Specifications</a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="/">Reviews</a>
      </li>
      <li className="nav-item">
      <a className="nav-link" href="/">Tags</a>
      </li>
    </ul>
  </div>
  <div className="card-body">
    <h5 className="card-title">Special title treatment</h5>
    <p className="card-text">With supporting text below as a natural lead-in to additional content.</p>
    <a href="/" className="btn btn-primary">Go somewhere</a>
  </div>
</div>

</div>
        )
    }
}

export default Detailview;
